import React, {Fragment} from 'react';

const Results = (props) => {
return (
	<div className="container">
		<div className="col s12 m6">
			<div className="card teal lighten-2">
				<div className="card-content black-text">

					{
						props.clima.error &&
								<Fragment>

						<a className="btn-floating btn-large pulse"><i className="material-icons">error</i></a>
						<p>Ocurrio un error</p>
						</Fragment>

					}
					{
						!props.clima.error  &&
					<table>
						<thead>
							<tr>
								<th>Tipo</th>
								<th>Resultados</th>
							</tr>
						</thead>
						<tbody>


							{
								props.clima?(
								<Fragment>
									
								<tr>
									<td>Temperatura</td>
									<td>{props.clima.temperature} °C</td>
								</tr>
								<tr>
									<td>Descripción</td>
									<td>{props.clima.description}</td>
								</tr>
								<tr>
									<td>Humedad</td>
									<td>{props.clima.humidity}</td>
								</tr>
								<tr>
									<td>Velocidad Viento</td>
									<td>{props.clima.wind_speed}</td>
								</tr>
								<tr>
									<td>Ciudad</td>
									<td>{props.clima.city}</td>
								</tr>
								<tr>
									<td>Pais</td>
									<td>{props.clima.country}</td>
								</tr>

								</Fragment>

								): (
			 						<tr>
			 							<td colSpan="2">No Data</td>
			 						</tr>
		 						)
							}
							
						</tbody>
					</table>
				}
				</div>
			</div>
		</div>
	</div>
);
}
export default Results;