import React, {useState} from 'react';
import Navbar from './components/layout/partials/Navbar'
import Footer from './components/layout/partials/Footer'

import Form from './components/pages/Form'
import Initial from './components/pages/Initial'
import Results from './components/pages/Results'

import './Styles.css';

function App() {
	const [resultado, setResultado] = useState(false);
	
	const handleChangeResultado = () => {
		setResultado(true);
	};

	const climaData = {
		temperature: "",
		description: "",
		humidity: "",
		wind_speed: "",
		city: "",
		country: "",
		error: true
	};

	// state
	const [clima, setClima] = useState(climaData);

	const showClima = (clima) =>{
		setClima(clima)
	}

	return (
		<div 	>
			<Navbar />
			<Form 
				handleChangeResultado={handleChangeResultado}
				showClima={showClima}
			 />

			{
				resultado ?
				<Results clima={clima} />
				:
				<Initial  />
			}
			 
			<Footer />
		</div>
	);
}

export default App;
